.row {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  font-family: sans-serif;
  overflow-x: hidden;
  position: relative;
  .cardPage {
    padding: 40px 0;
    .col {
      height: 264px;
      position: relative;
      &:before,
      &:after {
          content: '';
          display: block;
          width: 50%;
          height: 120%;
          background: linear-gradient(to right, rgba(255, 255, 255, 1) 15%, rgba(255, 255, 255, 0) 100%);
          position: absolute;
          top: 0;
          left: 0;
          z-index: 2;
      }
      &:after {
          left: auto;
          right: 0;
          background: linear-gradient(to left, rgba(255, 255, 255, 1) 15%, rgba(255, 255, 255, 0) 100%);
      }
      $i: 0;
      @for $i from 0 through 29 {
        .cardsSlider.activeSlide#{$i} #card#{$i} {
          opacity: 1;
          transform: scale(1);
          background-color: #f3f3f3;
          border-color: #f3f3f3;
          .details {
            opacity: 1;
          }
        }
      }

      .cardsSlider {
        position: relative;
        max-width: 226px;
        margin: 0 auto;
      
        // boder for active property
        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 264px;
          position: absolute;
          top: 0; 
          left: 0;
        }

        .cardsSliderWrapper {
          display: flex;
          position: absolute;
          transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
          // .card {
          //   flex: 1;
          //   min-width: 200px;
          //   opacity: 0.7;
          //   transform: scale(0.8);
          //   box-shadow: none;
          //   background-color: white;
          //   border-color: white;
          //   transition: opacity 300ms linear, border-color 300ms linear, background-color 300ms linear,transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
          //   .details {
          //     opacity: 0;
          //     transition: opacity 150ms linear;
          //   }
          // }
        }
      }
    }
  }
}


