.flexColumn {
  padding: 15px;
  display: flex;
  flex-direction: column;
  height: 414px;
  width: 100%;
  margin-bottom: 30px;
  .flexRowCenter {
    display: flex;
    flex-direction: row;
    align-items: center;
    h4 {
      font-size: 21px;
    }
    span {
      word-wrap: break-word;
      white-space: pre-wrap;
    }
  }
  
}


